.App {
  background-color: #82AABB;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  
  &_inner_container{
    display: flex;
    background-color: #403634;
    border: 7px solid #6D5A4E;
    border-radius: 3px;
    box-shadow: 1px 1px 2px black;
  }


  &_keys_container{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 5px;
    flex-wrap: wrap;
    background-color: #95806B;
    color: black;
    width: 16rem;
    align-items: center;
    justify-items: center;
    padding: 5px;
    
    
    .key{
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid black;
      background-color: #E9DBCE;
      width: 80px;
      height: 80px;
      margin: 0;
      border-radius: 5px;
      box-shadow: 1px 1px 5px black;
      user-select: none;
    }
    
    .key:active{
      box-shadow: 1px 1px 10px rgb(0, 0, 0) inset;
      
    }
  }

  &_settings_container{
    padding: 5px;
    background-color: #95806B;
  }

  &_display_sound{
    display: flex;
    align-items: center;
    height: 50px;
    background-color: gray;
    box-shadow: 1px 1px 1px black inset;
    border-radius: 5%;
    text-indent: 5px;

  }

  &_power_container{
    display: flex;
    align-items: center;
    margin-top: 10px;
  }
}

.activeClass{
      box-shadow: 1px 1px 10px black inset !important;
}

.switch{
      position: relative;
      display: inline-block;
      width: 60px;
      height: 34px;
  
    .switch input {
      opacity: 0;
      width: 0;
      height: 0;
    }
  
    .slider {
      position: absolute;
      cursor: pointer;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: #ccc;
      -webkit-transition: .4s;
      transition: .4s;
    }
  
    .slider:before {
      position: absolute;
      content: "";
      height: 26px;
      width: 26px;
      left: 4px;
      bottom: 4px;
      background-color: white;
      -webkit-transition: .4s;
      transition: .4s;
    }
  
    input:checked+.slider {
      background-color: #2196F3;
    }
  
    input:focus+.slider {
      box-shadow: 0 0 1px #2196F3;
    }
  
    input:checked+.slider:before {
      -webkit-transform: translateX(26px);
      -ms-transform: translateX(26px);
      transform: translateX(26px);
    }
  
    .slider.round {
      border-radius: 34px;
    }
  
    .slider.round:before {
      border-radius: 50%;
    }
}


@media screen and (max-width: 600px) {
  .App_inner_container {
    flex-direction: column;
  }
}